html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Avenir";
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.outliner {
  font-family: "Sifonn";
}

.naranja {
  color: #EC5E17;
}

.bgw1 {
  background-image: url("/src/img/Bg/BgW1.png");
  background-position-x: center;
  background-repeat: repeat-x;
  background-size: inherit;
}
@media (min-width: 992px) {
  .bgw1 {
    background-size: contain;
  }
}

.bgw2 {
  background-image: url("/src/img/Bg/BgW2.png");
  background-position-x: center;
  background-size: inherit;
}
@media (min-width: 992px) {
  .bgw2 {
    background-size: contain;
  }
}

.bgb1 {
  background-image: url("/src/img/Bg/BgB1.png");
  background-position-x: center;
  background-repeat: repeat-x;
  color: #FFFFFF;
  background-size: inherit;
}
@media (min-width: 992px) {
  .bgb1 {
    background-size: contain;
  }
}

.bgb2 {
  color: #FFFFFF;
  background-image: url("/src/img/Bg/BgB2.png");
  background-position-x: center;
  background-size: inherit;
}
@media (min-width: 992px) {
  .bgb2 {
    background-size: contain;
  }
}

.header {
  background-color: #000000;
  z-index: 2;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.3rem #333;
  }
}
.header h1 {
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenido-header {
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .header .contenido-header {
    justify-content: space-between;
    align-items: center;
  }
}

.navegacion-principal {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  align-items: center;
}
.navegacion-principal img {
  width: 10rem;
}
@media (min-width: 768px) {
  .navegacion-principal {
    padding: 0;
  }
}

.nav-menu {
  z-index: 2;
  list-style: none;
  position: fixed;
  width: 100%;
  text-align: center;
  top: 4rem;
  left: 0;
  background-color: black;
  padding: 0;
  left: 100%;
  transition: left 0.3s;
  height: 100%;
}
@media (min-width: 768px) {
  .nav-menu {
    width: auto;
    margin: 1rem;
    position: inherit;
    display: flex;
  }
}

.nav-menu-visible {
  left: 0;
}

.nav-menu-link {
  text-align: center;
  color: #FFFFFF;
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .nav-menu-link {
    margin: 0;
  }
}
.nav-menu-link:hover {
  color: #FEF46D;
}

.nav-menu-item {
  margin: 1rem 1rem;
}

.nav-toggle {
  color: white;
  background: black;
  border: none;
  font-size: 4rem;
  display: block;
}
@media (min-width: 768px) {
  .nav-toggle {
    display: none;
  }
}

.video {
  position: relative;
  max-height: 100%;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 1;
}
.contenido-video h2 {
  color: #FFFFFF;
  font-size: 6rem;
  margin: 0;
}
@media (min-width: 768px) {
  .contenido-video h2 {
    font-size: 7rem;
  }
}
.contenido-video p {
  background-color: #F53756;
  display: inline-block;
  padding: 1rem 2rem;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
}

.festival .importante {
  padding: 15rem 0;
}
.festival .sobre-marketing {
  padding: 15rem 0;
}
.festival .sobre-marketing h2 {
  text-align: center;
}
.festival .sobre-marketing p {
  padding: 2rem 10rem;
  color: black;
  text-align: center;
  margin-bottom: 0;
  background-color: #FFFFFF;
}
.festival .porque-streaming {
  padding: 15rem 0;
}
.festival .porque-streaming h2 {
  text-align: center;
}
@media (min-width: 768px) {
  .festival .porque-streaming .contenido-porque {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.festival .porque {
  display: grid;
}
.festival .porcentajes {
  padding: 15rem 0;
}
.festival .porcentajes p {
  color: white;
}
.festival .porcentajes h2 {
  font-size: 15rem;
  margin-bottom: 0;
  margin-top: 4rem;
  text-align: center;
}
