}

.sobre-festival {
  align-items: center;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.titulo h2 {
  margin: 0rem;
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h3 {
  margin-bottom: 0;
}
.contenido-festival .fecha {
  color: #F53756;
  font-weight: 700;
}

